<template>
  <van-row>
    <!-- 顶部框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">考勤统计分析</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <!-- 数据显示区域 -->
      <van-row class="dt-nav">
        <van-col :span="8"
                 class="dt-data-box1">
          <div class="dt-font">志愿者</div>
          <div class="dt-data">{{zhiyuanzhe}}</div>
        </van-col>
        <van-col :span="8"
                 class="dt-data-box2">
          <div class="dt-font">安保</div>
          <div class="dt-data">{{anbao}}</div>
        </van-col>
        <van-col :span="8"
                 class="dt-data-box3">
          <div class="dt-font">保洁</div>
          <div class="dt-data">{{baojie}}</div>
        </van-col>
      </van-row>
      <!-- 考勤统计 -->
      <van-row>
        <!-- 标题 -->
        <van-row>
          <div class="dt-chartTitle">
            <span>考勤分析(最近七日)</span>
          </div>
        </van-row>
        <!-- 按钮 -->
        <!-- <van-row type="flex"
                 justify="end">
          <van-col span="20"
                   class="dt-buttonBox">
            <van-button @click="withinsixDays">7天内</van-button>
            <van-button @click="withinOneMonth">30天内</van-button>
            <van-button @click="withinThreeMonths">3个月内</van-button>
            <van-button @click="WithinOneYear">1年内</van-button>
          </van-col>
        </van-row> -->
        <!-- 柱状图 -->
        <van-row>
          <div style="width:100%;height:212.5px"
               id="echarts"></div>
          <!-- <div class="dt-TPA-Histogram">
            <atS :option="option"
                 title="考勤分析（最近七日）"></atS>
          </div> -->
        </van-row>
      </van-row>
    </van-row>
  </van-row>
</template>


<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { GetCheckDutySatatic, GetDataBySearch, GetCheckDutyRealTimeSatatic } from "@/api/reportStatistics";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
  },
  data () {
    //这里存放数据
    return {
      anbao: '',
      baojie: '',
      zhiyuanzhe: '',
      option: {},
      valueList: {
        volunteerValue: [0, 0, 0, 0, 0, 0, 0],
        securityValue: [0, 0, 0, 0, 0, 0, 0],
        cleanValue: [0, 0, 0, 0, 0, 0, 0],
        dateValue: [0, 0, 0, 0, 0, 0, 0]
      }

    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.setEcharts()
    this.getData()
    this.getSecond()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //获取数据
    async getData () {
      //
      let info = {
        SysCode: "HomeView",
        Key: "CheckWorkStatic"
      }
      GetDataBySearch(info).then(res => {
        let data = res.Data.UValue
        let d = JSON.parse(data);
        var serviceNum = 0
        var securityNum = 0
        var cleanNum = 0
        for (let index in d) {
          if (d[index].Description == "保洁") {
            cleanNum = d[index].ItemValue
          } else if (d[index].Description == "志愿者") {
            serviceNum = d[index].ItemValue
          } else if (d[index].Description == "安保") {
            securityNum = d[index].ItemValue
          }
        }
        //
        GetCheckDutyRealTimeSatatic(info).then(res => {
          let data = res.Data
          let clean = 0
          let service = 0
          let security = 0
          for (let index in data) {
            if (data[index].CheckUserDuty == "服务保障") {
              clean = data[index].Expend1
            } else if (data[index].CheckUserDuty == "志愿者") {
              service = data[index].Expend1
            } else if (data[index].CheckUserDuty == "安保") {
              security = data[index].Expend1
            }
          }
          let serviceNumber = Number(serviceNum) + service
          let securityNumber = Number(securityNum) + security
          let cleanNumber = Number(cleanNum) + clean
          this.zhiyuanzhe = serviceNumber;
          this.anbao = securityNumber;
          this.baojie = cleanNumber;
        })
      })
    },



    //
    getSecond () {
      let startTime = new Date(new Date(new Date().toLocaleDateString()).setDate(new Date().getDate() - 6)).format("yyyy-MM-ddT00:00:00");
      let endTime = new Date().format("yyyy-MM-ddT23:59:59")
      let info = {
        "CheckBeginDate": startTime,
        "CheckEndDate": endTime
      }
      GetCheckDutySatatic(info).then(response => {
        let data = response.Data
        //时间例表
        let timeList = []
        let dateList = []
        for (let i = 6; i >= 0; i--) {
          let time = new Date(new Date(new Date().toLocaleDateString()).setDate(new Date().getDate() - i)).format("yyyy-MM-ddT00:00:00");
          timeList.push(time)
          let date = new Date(new Date(new Date().toLocaleDateString()).setDate(new Date().getDate() - i)).format("MM-dd")
          dateList.push(date)
        }

        let clean = [0, 0, 0, 0, 0, 0, 0]
        let service = [0, 0, 0, 0, 0, 0, 0]
        let security = [0, 0, 0, 0, 0, 0, 0]
        for (let index in data) {
          let CheckDate = data[index].CheckDate

          for (let dex in timeList) {
            if (timeList[dex] == CheckDate) {
              let CheckUserDuty = data[index].CheckUserDuty
              if (CheckUserDuty == "园内保洁" || CheckUserDuty == "园外保洁") {
                clean[dex] += data[index].Expend1
              } else if (CheckUserDuty == "园区游客服务") {
                service[dex] += data[index].Expend1
              } else if (CheckUserDuty == "园内安保" || CheckUserDuty == "园外安保") {
                security[dex] += data[index].Expend1
              }
            }
          }
        }

        // console.warn("统计！！！！！！", service, security, clean);

        let value = {
          volunteerValue: service,
          securityValue: security,
          cleanValue: clean,
          dateValue: dateList
        }
        this.valueList = value
        this.setEcharts()
      })


    },

    setEcharts () {
      var myChart = this.$echarts.init(document.getElementById('echarts'));
      // 指定图表的配置项和数据
      var option = {
        legend: {
          data: ['志愿者', "安保", "保洁"],
          x: 'right',      //可设定图例在左、右、居中
          y: 'bottom',
          itemHeight: 12, // 设置图例高度
          textStyle: {
            fontSize: 12,//设置图例文字高低
            color: "#333333",
          },
        },
        grid: {
          left: 4, // 默认10%，给24就挺合适
          top: 14, // 默认60
          right: 4, // 默认10%
          bottom: 24, // 默认60
          containLabel: true
        },


        xAxis: {
          type: 'category',
          data: this.valueList.dateValue,
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {  // 设置轴标签
            // rotate: -40,
            fontSize: 11,//横坐标字体大小
            interval: 0,  // 隔几个显示一个，为0强制全显示
          },
        },
        yAxis: {
          type: 'value',
          splitNumber: 9,
          minInterval: 1,
          axisLabel: {
            formatter: '{value}人'
          }
        },
        series: [
          {
            name: '志愿者',
            data: this.valueList.volunteerValue,
            type: 'bar',
            stack: '0',
            label: {//数据是否显示和具体属性设置
              show: false,
              position: 'insideRight'
            },
          },
          {
            name: '安保',
            data: this.valueList.securityValue,
            type: 'bar',
            stack: '0',
            label: {//数据是否显示和具体属性设置
              show: false,
              position: 'insideRight'
            },
          },
          {
            name: '保洁',
            data: this.valueList.cleanValue,
            type: 'bar',
            stack: '0',
            label: {//数据是否显示和具体属性设置
              show: false,
              position: 'insideRight'
            },
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    },
    Home () {
      this.$router.push({ name: 'Home' })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
