  background-image: url('../../../assets/img/保安.png');
  background-repeat: no-repeat;
  margin-top: px2rem(11.5);
}
.dt-data-box3 {
  background-position: 50% 50%;
  height: px2rem(81.5);
  background-image: url('../../../assets/img/保洁.png');
  background-repeat: no-repeat;
  margin-top: px2rem(11.5);
}
.dt-font {
  font-size: px2rem(18);
  font-family: 'PingFang-SC-Medium';
